import { OnInit } from '@angular/core';
import { OntologyService, ProjectsService } from '@knora/core';
var OntologyPgComponent = /** @class */ (function () {
    function OntologyPgComponent(_projectsService, _ontologyService) {
        this._projectsService = _projectsService;
        this._ontologyService = _ontologyService;
        this.loading = true;
        this.disabled = true;
        this.projectcode = '0001';
        this.itemPluralMapping = {
            'ontology': {
                '=1': '1 Ontology',
                'other': '# Ontologies'
            }
        };
    }
    OntologyPgComponent.prototype.ngOnInit = function () {
        // get project, to get a list of ontologies
        var _this = this;
        this._projectsService.getProjectByShortcode(this.projectcode).subscribe(function (response) {
            _this.project = response;
            _this.loadOntologies(_this.project.id);
        }, function (error) {
            console.error(error);
            _this.loading = false;
        });
    };
    OntologyPgComponent.prototype.loadOntologies = function (id) {
        var _this = this;
        // get the project ontologies by project id (iri)
        this._ontologyService.getProjectOntologies(id).subscribe(function (result) {
            if (result.body['@graph']) {
                _this.ontologies = result.body['@graph'];
            }
            else {
                var ontology = {
                    '@id': result.body['@id'],
                    '@type': result.body['@type'],
                    'knora-api:attachedToProject': result.body['knora-api:attachedToProject'],
                    'knora-api:lastModificationDate': result.body['knora-api:lastModificationDate'],
                    'rdfs:label': result.body['rdfs:label']
                };
                _this.ontologies = [ontology];
            }
            _this.loading = false;
        }, function (error) {
            console.error(error);
        });
    };
    OntologyPgComponent.prototype.removeOntology = function (id) {
        alert('This will deactivate the ontology: ' + id + '(This doesn\'t work here!)');
    };
    OntologyPgComponent.prototype.createOntology = function () {
        var _this = this;
        this.loading = true;
        var something = Math.floor(Math.random() * Math.floor(9999));
        var ontologyData = {
            projectIri: this.project.id,
            name: this.project.shortname + '-data-model-' + something,
            label: 'Data Model (Ontology) for ' + this.project.shortname + ' (N° ' + something + ')'
        };
        this._ontologyService.createOntology(ontologyData).subscribe(function (ontology) {
            _this.loadOntologies(_this.project.id);
        }, function (error) {
            console.error(error);
        });
    };
    return OntologyPgComponent;
}());
export { OntologyPgComponent };
